
/*  big tablets to 1200px (width smaller than the 1140px row) */

@media only screen and (max-width: 1200px){
    .hero-text-box{
      width: 100%;
      padding: 0 2%;
     }
      .row {
          padding: 0 2%;
      }
  }
  
  
  /*  small tablets to big tablets from  768px to 1023px */
  
  @media only screen and (max-width: 1023px){
      .about-image{
          width: 130px !important;
          height: 130px !important;
      }
      body {
          font-size : 18px;
      }
      section {
          padding: 60px 0;
      }
          
      .long-copy {
      line-height: 145%;
      width: 80%;
      margin-left: 10%;
     }
      
      .steps-box { margin-top: 10px;}
      .steps-box:last-child {margin-top: 10px;}
      .works-steps {
          margin-bottom: 40px;
      }
     .works-step:last-of-type{
      margin-bottom: 60px;
      }
      
      .app-screen {width: 50%;}
      .ion-small {
          margin-right: 10px;
          width: 20px;
      }
      .city-feature {
          font-size: 90%;
      }
      
      .plan-box {
          width: 100%;
          margin-left: 0%;
      }
      
      .plan-price {font-size: 250%;}
      
      .contact-form {
          width: 80%;
      }
  }
  
  
  /*  small phones to small tablets 481px to 767px */
  
  @media only screen and (max-width: 767px){
      body{font-size: 14px !important;}
      .about-image{ 
          height: 150px !important;
          width: 150px !important;
          padding-bottom: 10px !important;
      }
      section{padding: 30px 0;}
      .row,.hero-text-box{
          padding: 0 4%;
      }
      .col {
          width: 100%;
          margin: 0 0 4% 0%;
      }
      .main-nav {display: none;}
      .onlynotonlappy{
        display: block !important;
        visibility: visible;
    }
    .onlyonlappy{
        display: none !important;
        visibility: hidden;
    }
      .long-copy{
          width: 100%;
          margin-left: 0%;
      }
      h1 {
          font-size: 180%;;
      }
       h2 {
          font-size: 150%;;
      }
    
      .works-steps {
          margin-bottom: 20px;
      }
     .works-step:last-of-type{
      margin-bottom: 20px;
      }
      .works-step div{
      height: 40px;
      width: 40px;
      padding: 4px;
      margin-right: 15px;
      font-size: 120%;
      }
      .app-screen {
          width: 40%;
      }
      .steps-box:first-child {
      text-align: center;
    }
  }
  
  /* small phones from 0 to 480px */
  
  @media only screen and (max-width: 480px){
      section {
          padding: 25px 0;
      }
      .contact-form{
          width: 100%;
      }
  }